import React, { ReactElement } from "react";
import SvgIcon from "@mui/material/SvgIcon";
import {
  Dialog,
  DialogContent,
  DialogTitle,
  Paper,
  Stack,
  Typography,
} from "@mui/material";
import ProjectDialogTab from "./projectDialogTab";
import ProjectCardText from "./projectCardText";

interface ProjectsDialogProps {
  project: {
    id: string;
    label: string;
    level: string;
    year: string;
    collaborators: [string];
    description: string;
    personnalPartDone: string;
    techs_used: [string];
    avatar: JSX.Element;
  };
  open: boolean;
  handleClose: (value:string)=>void;
}

export default function ProjectsDialog(props: ProjectsDialogProps) {
  return (
    <Dialog
      onClose={props.handleClose}
      open={props.open}
      style={{
        padding: "16px",
        minWidth: "280dp",
        maxWidth: "560dp",
      }}
      >
      <DialogTitle
        style={{
          maxHeight: "56px",
          display: "flex",
          justifyContent: "center",
        }}
      >
        {props.project.avatar}
        {props.project.label}
      </DialogTitle>
      <DialogContent>
        <ProjectCardText
          id="project_state"
          title="tat du project"
          text={props.project.level}
        />
        <br/>
        <ProjectCardText
          id="project_description"
          title="Description du projet"
          text={props.project.description}
        />
        <br/>
        <ProjectCardText
          id="personnal_work_in_project"
          title="Travail personnel ralis"
          text={props.project.personnalPartDone}
        />
        <br/>
        <ProjectDialogTab
          id="other_members_of_project"
          tabTitle="Autres membres du projet"
          emptyMessage="Aucun autre membre n'a travaill sur ce projet!"
          tabCellList={props.project.collaborators}
        />
        <br/>
        <ProjectDialogTab
          id="techs_used_in_project"
          tabTitle="Technologies utilises"
          emptyMessage="Aucune technologie utilise dans ce projet."
          tabCellList={props.project.techs_used}
        />
      </DialogContent>
    </Dialog>
  );
}
