import React from 'react';

import { Paper, Table, TableBody, TableCell, TableContainer, TableRow, Typography } from '@mui/material';
import ProjectCardText from './projectCardText';

interface ProjectDialogTabProps{
    id:string;
    tabTitle:string;
    tabCellList: [string] | undefined;
    emptyMessage: string;
}

export default function ProjectDialogTab(props:ProjectDialogTabProps){
    const cellList = props.tabCellList;
    const emptyMessage = props.emptyMessage;
    const tabTitle = props.tabTitle

    if(cellList == undefined || cellList == null || cellList.length == 0){
        return(
            <ProjectCardText
            id={props.id}
            title={tabTitle}
            text={emptyMessage}
          />
        )
    }else{
        return(
            <div key={props.id}>
                <Typography variant="h6">
                    {tabTitle}
                </Typography>
                <TableContainer component={Paper}>
                    <Table aria-label="simple table">
                        <TableBody>
                            {cellList.map((singleCellItem,i)=>{
                                return(
                                    <TableRow key={"single_collaborator_"+i}>
                                        <TableCell align="left">
                                            {singleCellItem}
                                        </TableCell>
                                    </TableRow>
                                )
                            })}
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
        );
    }
}