interface ProjectsTabState{
    open: boolean;
    setOpen: boolean;
    selectedId: string;
    setSelectedID: string;
}

interface ProjectsTabProps{}

class ProjectsTab extends React.Component<ProjectsTabProps,ProjectsTabState>{
    constructor(props:ProjectsTabProps){
        super(props);
        this.state={
            open:false,
            setOpen:false,
            selectedId:"",
            setSelectedID: ""
        }
    }

    fetchList = (id : string) => {
        var found = false;
        var value;
        projects.map((singleProjectList,_)=>{
            singleProjectList.list.map((singleProject,__)=>{
                if(id === singleProject.id){
                    found = true;
                    value = singleProject;
                    return value;
                }
            })
        })
        if(found == false){
            return({
                id:'',
                label:'',
                level:'',
                year:'',
                description: '',
                avatar: <Computer/>,
                personnalPartDone: '',
                collaborators: [''],
                techs_used: ['']
            })
        }else{
            return value;
        }
    }

    handleClose = (value:string) => {
        this.setState({
            setOpen:false,
            open:false,
            setSelectedID: value
        })
    }

    handleOpen = (id:string) => {
        this.setState({
            selectedId:id,
            setOpen:true,
            open:true,
        })
    }

    render(){
        const state = this.state;
        return(
            <div id = "projects"
                style={{
                    maxHeight: "85vh",
                    overflow: "auto"
                }}
            >
              <Box display="grid"
                gridTemplateColumns="repeate(12,1fr)"
                gap={16}>
                <Grid
                    container
                    direction="column"
                    style={{gap:15}}
                    justifyContent={"space-between"}
                    alignItems={"stretch"}
                >
                    {projects.map((singleProjectList,i)=>{
                        return(
                            <ProjectCardList
                                i={i}
                                label={singleProjectList.label}
                                projectList={singleProjectList.list}
                                onClick={this.handleOpen}
                            />
                        )
                    })}
                </Grid>
              </Box>
              <ProjectsDialog
                project={this.fetchList(state.selectedId)}
                handleClose={this.handleClose}
                open={state.open}
              />
            </div>
        );
    }
}


export default ProjectsTab;