import { Card, Grid, Typography } from "@mui/material";
import React, { ReactNode } from "react";
import ProjectCard from "./projectCard";

interface ProjectCardListProps{
    i:number;
    label:string;
    projectList:{
        id:string,
        label:string,
        level:string,
        year:string,
        collaborators:string[] | undefined,
        description: string,
        personnalPartDone: string,
        avatar: ReactNode
        techs_used:string[] | undefined
    }[];
    onClick:(value:string)=>void;
}

export default function ProjectCardList(props:ProjectCardListProps){
    return(
        <div
        id={"singleSkill"+props.i}
        key={"singleSkill"+props.i}>
            <Grid
                item>
            <Typography variant="h5">
                {props.label}
            </Typography>
                <Grid
                    container
                    direction="row"
                    >
                        {props.projectList.map((singleProject,j)=>{
                            return(
                                <ProjectCard
                                id={singleProject.id}
                                i={props.i}
                                j={j}
                                avatar={singleProject.avatar}
                                label={singleProject.label}
                                year={singleProject.year}
                                description={singleProject.description}
                                onClick={props.onClick}
                            />
                            )
                        })}
                </Grid>
            </Grid>
        </div>
    )
}