import React from "react";

import {Paper, Typography} from '@mui/material';

interface ProjectCardTextLabel{
    id:string;
    title:String;
    text:String;
}

export default function ProjectCardText(props:ProjectCardTextLabel){
    return(
        <div key={props.id}>
            <Typography variant="h6">{props.title}</Typography>
            <Paper
            elevation={2}
            style={{
                padding:"8px",
                margin: "8px 0"
            }}
            >
            {props.text}
            </Paper>
        </div>
    )
}